<!--   -->
<template>
  <div id="ct">
    <section class="card">
      <header class="card-header">
        <h2 class="card-title">
          <span class="i-rounded bg-danger"><i class="icon-user" /></span>
        </h2>
        <h2 class="card-title text-tertiary text-pad">
          |
        </h2>
        <a href="/point">
          <h2 class="card-title text-tertiary">
            
          </h2>
        </a>
        <div class="btn-container">
          <a
            class="btn btn-m"
            @click="() => {this.$router.go(-1)}"
          ><span class="hide"></span></a>
          <a
            class="btn btn-primary"
            @click="submitForm"
          ><span
            class="hide"
            style="color:white;"
          ></span></a>
        </div>
      </header>
      <!--  -->
      <div class="card-body card-img">
        <div
          v-if="noImage()"
          class="profile_management_sample"
        >
          <i
            class="icon-pic"
          />
          <span class="text-secondary">No image loaded</span>
        </div>
        <!-- DB     v-else -->
        <v-img
          v-else
          :src="get_img_src"
        />
      </div>
      <div
        class="table-responsive"
        style="width: 30rem"
      >
        <table class="table">
          <colgroup>
            <col style="width: 30%">
            <col style="width: 70%">
          </colgroup>
          <tbody>
            <tr>
              <td class="alert-text">
                
              </td>
              <td>{{ user_name }}</td>
            </tr>
            <tr>
              <td class="alert-text">
                
              </td>
              <td>{{ user_id }}</td>
            </tr>
            <tr>
              <td class="alert-text">
                
              </td>
              <td>{{ company }}</td>
            </tr>
            <tr>
              <td class="alert-text">
                
              </td>
              <td>
                <div class="form-group">
                  <input
                    v-model="dept"
                    type="text"
                    class="form-control"
                    placeholder=" ."
                  >
                </div>
              </td>
            </tr>
            <tr>
              <td class="alert-text">
                
              </td>
              <td>
                <select
                  v-model="solution"
                  class="form-control"
                  title=""
                  @change="selectSolution"
                >
                  <option
                    v-for="(item,index) in items"
                    :key="index"
                    :value="item.codeId"
                  >
                    {{ item.codeContent }}
                  </option>
                </select>
                <!--
                <v-select
                  v-model="solution"
                  class="form-control compact-form"
                  :items="items"
                  dense
                  solo
                  item-text="codeContent"
                  item-value="codeId"
                  @change="selectSolution"
                /> -->
              </td>
            </tr>
            <tr>
              <td class="alert-text">
                
              </td>
              <td>
                <select
                  v-model="user_type"
                  class="form-control"
                  title=""
                  @change="selectType"
                >
                  <option
                    v-for="(item,index) in items2"
                    :key="index"
                    :value="item.code"
                  >
                    {{ item.name }}
                  </option>
                </select>
                <!-- <v-select
                  v-model="user_type"
                  class="form-control compact-form"
                  :items="items2"
                  dense
                  solo
                  item-text="name"
                  item-value="code"
                  @change="selectType"
                /> -->
              </td>
            </tr>
            <tr>
              <td class="alert-text">
                 
              </td>
              <td>
                <div class="filebox">
                  <label
                    class="btn btn-m"
                    for="ex_file"
                  ></label>
                  <v-file-input
                    id="ex_file"
                    v-model="image"
                    hide-input
                    type="file"
                    accept="image/*"
                    @change="onUpload"
                  />
                </div>
              </td>
            </tr>

            <tr>
              <td class="alert-text">
                
              </td>
              <td>
                <a
                  class="btn mr-2 mb-2"
                  data-toggle="modal"
                  @click.prevent="addBoard"
                ><span class="hide"></span></a>
              </td>
            </tr>
          </tbody>
        </table>
        <Modal
          :dialog="isAddBoard"
          @close="isAddBoard=false"
        />
        <Alert
          :dialog="completeAlert"
          :send-data="alertContent"
          @close="completeAlert=false"
        />
      </div>
    </section>
  </div>
</template>

<script>
import { userType, userSolution, getSolution, expToken } from '@/api/log/Login.js'
import { updateProfile, selectProfile } from '@/api/prm/Profile.js'
import { formData } from '@/api/log/Signup.js'
import Modal from '@/components/prm/KAPRM0103.vue' // 1.   
import Alert from '@/components/common/CompletePOP.vue' //  alert

export default {
  components: {
    Modal,
    Alert
  },
  data: () => {
    return {
      alertContent: '',
      showNoImage: false,
      isAddBoard: false,
      completeAlert: false,
      imgSrc: '',
      image: null,
      code: '',
      typeCode: '',
      alert: false,
      dept: '',
      user_type: '',
      solution: '',
      items: [],
      items2: [
        {
          name: '',
          code: 'A'
        },
        {
          name: '',
          code: 'Q'
        }
      ]
    }
  },
  computed: {
    get_img_src () {
      return 'data:image;base64,' + this.imgSrc
    },
    user_name: function () {
      return this.$store.state.username
    },
    user_id: function () {
      return this.$store.state.userid
    },
    company: function () {
      return this.$store.state.company
    }
  },
  async mounted () {
    console.log('mounted')
    const userdata = {
      user_id: this.$store.state.userid
    }
    const { data } = await selectProfile(userdata)
    if (data === '') {
      this.showNoImage = true
    } else {
      this.imgSrc = data
    }
  },
  async created () {
    const exp = this.$store.state.exp
    expToken(exp)
    this.$store.commit('SET_DEPTH1', '')
    this.$store.commit('SET_DEPTH2', '')

    const { data } = await getSolution() //   

    console.log('data: ', data)
    this.items = data
    this.userType()
    console.log('', this.user_type)

    this.code = this.$store.state.solution //   
    console.log('code?' + this.code)

    this.solution = userSolution(this.code)
    console.log('?' + this.solution) //   

    this.dept = this.$store.state.dept
    console.log('this.items', this.items)

    this.solution = this.code
    console.log('this.solution', this.solution)
  },
  methods: {
    selectSolution () {
      this.code = this.solution //   solution code
    },
    selectType () {
      this.typeCode = this.user_type //   user_type code
    },
    userType () {
      this.typeCode = this.$store.state.usertype
      this.user_type = userType(this.typeCode)
      console.log('this.typeCode', this.typeCode)
      console.log('this.user_type', this.user_type)
      this.user_type = this.typeCode
    },
    addBoard () {
      this.isAddBoard = true
      console.log(this.isAddBoard)
    },
    submitForm () { //   
      console.log('submitForm ')

      const userData = {
        dept: this.dept,
        solution: this.code,
        user_type: this.typeCode,
        user_id: this.user_id
      }

      console.log(' ?', this.solution)
      console.log('userData', userData)
      updateProfile(userData)
        .then((res) => {
          if (res.status === 200) {
            console.log('status200 ?' + res.status)
            console.log('this.code??' + this.code)

            //      vue  data ..
            this.$store.commit('SET_DEPT', userData.dept)
            this.$store.commit('SET_SOLUTION', this.code)
            this.$store.commit('SET_USERTYPE', this.typeCode)
            console.log('' + this.typeCode)

            this.alertContent = ' '
            this.completeAlert = true
          }
        }
        )
    },
    async onUpload () {
      try {
        var fd = new FormData()
        const allowedTypes = ['image/jpeg', 'image/jpg', 'image/png'] //   
        if (!allowedTypes.includes(this.image.type)) {
          this.alert = true //     alert  
        } else {
          this.alert = false
          console.log(' ')
          console.log(this.image.type === 'image/*')
          fd.append('profile_image', this.image)
          fd.append('user_id', this.user_id)
          const { data } = await formData(fd)
          console.log('???', data)
          this.imgSrc = data
          this.showNoImage = false
        }
      } catch (error) {
      }
    },
    noImage () {
      return this.showNoImage
    }
  }
}
</script>

<style>
.form-group{position: relative}
.form-group i{position: absolute; right: 9px; top: 9px;}
.form-group img{ width: 1.2rem; position: absolute; right: 7px; top: 7px}

.filebox v-file-input[type="file"] {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.theme--light.v-file-input{
  display: none;
}
</style>
